import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import './index.scss';

import Controller from '../../../../tools/Controller';

export default class index extends Component {
	constructor(props) {
		super(props);
		this.state = {
			props: props,
		};

		this.emailRef = React.createRef();
		this.passwordRef = React.createRef();

		this.registerNameRef = React.createRef();
		this.registerEmailRef = React.createRef();
		this.registerPasswordRef = React.createRef();

		this.register = this.register.bind(this);
		this.login = this.login.bind(this);
	}

	register() {
		Controller.register(
			this.registerNameRef.current.value,
			this.registerEmailRef.current.value,
			this.registerPasswordRef.current.value
		);
	}

	login() {
		Controller.login(this.emailRef.current.value, this.passwordRef.current.value);
	}

	render() {
		return (
			<Modal
				show={this.props.show}
				onHide={this.props.onHide}
				size=""
				aria-labelledby="contained-modal-title-vcenter"
				centered
			>
				<div style={{ display: this.props.loginCondition ? 'block' : 'none' }}>
					<Modal.Header closeButton>
						<Modal.Title id="contained-modal-title-vcenter"></Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<div className="Auth-form-container">
							<form className="Auth-form">
								<div className="Auth-form-content">
									<h3 className="Auth-form-title text-center"></h3>
									<div className="text-center">
										?{' '}
										<span className="link-primary cursorPointer" onClick={() => this.props.changeLoginMode(false)}>
											
										</span>
									</div>
									<div className="form-group mt-3">
										<label></label>
										<input
											type="email"
											className="form-control mt-1"
											placeholder="Enter email"
											ref={this.emailRef}
											defaultValue="root"
											required
										/>
									</div>
									<div className="form-group mt-3">
										<label></label>
										<input
											type="password"
											className="form-control mt-1"
											placeholder="Enter password"
											ref={this.passwordRef}
											defaultValue="12345678"
											required
										/>
									</div>
									<div className="d-grid gap-2 mt-3">
										<button className="btn btn-primary" onClick={this.login}>
											
										</button>
									</div>
									<p className="text-center mt-2">
										 <a href="#">?</a>
									</p>
								</div>
							</form>
						</div>
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.props.onHide}></Button>
					</Modal.Footer>
				</div>

				<div style={{ display: !this.props.loginCondition ? 'block' : 'none' }}>
					<Modal.Header closeButton></Modal.Header>
					<Modal.Body>
						<div className="Auth-form-container">
							<form className="Auth-form">
								<div className="Auth-form-content">
									<h3 className="Auth-form-title text-center"></h3>
									<div className="text-center">
										?{' '}
										<span className="link-primary cursorPointer" onClick={() => this.props.changeLoginMode(true)}>
											
										</span>
									</div>
									<div className="form-group mt-3">
										<label></label>
										<input
											type="text"
											className="form-control mt-1"
											placeholder="name"
											ref={this.registerNameRef}
											required
											defaultValue="JJ"
										/>
									</div>
									<div className="form-group mt-3">
										<label></label>
										<input
											type="email"
											className="form-control mt-1"
											placeholder="Email Address"
											ref={this.registerEmailRef}
											required
											defaultValue="123@gmail.com"
										/>
									</div>
									<div className="form-group mt-3">
										<label></label>
										<input
											type="password"
											className="form-control mt-1"
											placeholder="Password"
											ref={this.registerPasswordRef}
											required
											defaultValue="12345678"
										/>
									</div>
									<div className="d-grid gap-2 mt-3">
										<button type="submit" className="btn btn-primary" onClick={this.register}>
											
										</button>
									</div>
									<p className="text-center mt-2">
										 <a href="#">?</a>
									</p>
								</div>
							</form>
						</div>
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.props.onHide}></Button>
					</Modal.Footer>
				</div>
			</Modal>
		);
	}
}
